.skillDiv{
  flex-wrap: wrap;
  width: 100%;
  height: 500px;
}
h1{
  width: 100%;
  height: 50px;
  font-size: 40px;
  color: #6b799e;
  margin-bottom: 0px;
}
.titleAboutSkill{
  font-size: 30px;
  color: #6b799e;
  width: 20%;
  margin-top: 20px;
}
.majorSkillDiv{
  margin-top: 20px;
  width: 80%;
  height: 130px;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.majorSkillDiv > span{
  border-radius: 50%;
  width: 130px;
  height: 130px;
  background-color: #9c8f96;
  color: #ebc57c; 
  font-size: 40px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}
.handledSkillDiv{
  margin-top: 20px;
  color: #9c8f96;
  width: 80%;
  height: 130px;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.handledSkillDiv > span{
  border-radius: 50%;
  width: 80px;
  height: 80px;
  background-color: #9c8f96;
  color: #ebc57c; 
  font-size: 20px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}



